






















import contactInfo from "@/assets/json/Contact.json"
import emitter from "@/services/emitter"
import Vue from 'vue';
export default Vue.extend({
  name: 'Contact',
  components: {
  },
  data: () => ({
    contactInfo,
    showSheet: false
  }),
  created(){
    emitter.on('toggleContact', ()=>{ this.showSheet = !this.showSheet })
  }
});



