













import { kebabCase, floor } from "lodash";
import Vue from 'vue';

const CARD_WIDTH = 300
const CARD_MIN_WIDTH = 200
export default Vue.extend({
  name: 'CardList',
  props: {
    width: {
      type: Number,
      required: true
    },
    items: {
      type: Array,
      required: true
    }
  },
  methods: {
    kebabCase
  },
  computed: {
    cardWidth() : any {
      let width = (this.width - 1) / floor(this.width / CARD_WIDTH)
      return {
        width,
        "min-width": CARD_MIN_WIDTH
      }
    }
  }
});



