











import Vue from 'vue';
import emitter from "@/services/emitter"
export default Vue.extend({
  name: 'Footer',
  data: function() {
    return {
      route: this.$router.currentRoute.name
    }
  },
  methods: {
    toggleContact(){
      emitter.emit('toggleContact');
    }
  },
  watch: {
    $route(to, from) {
      this.route = to.name 
    }
  }
});
